.contact-page {
	width: 100%;
	height: 95vh;
	align-content: center;
	background-color: #edf2f4;
}
.contact-form {
	align-content: center;
	padding-top: 8%;
	padding-left: 30%;
	padding-right: 30%;
	width: auto;
}
.contactTitle {
	font-size: 2em;
	margin-bottom: 50px;
	margin-top: 50px;
	text-align: center;
}

@media only screen and (max-width: 375px) {
	.contact-page {
		width: 100%;
		height: 95vh;
		align-content: center;
		background-color: #edf2f4;
	}
	.contact-form {
		align-content: center;
		padding-top: 50px;
		padding-left: 3%;
		padding-right: 3%;
		width: auto;
	}

	.contactTitle {
		font-size: 1.5em;
		margin-bottom: 50px;
		margin-top: 50px;
		text-align: center;
	}
}
